    box-shadow: none;
    transform: none;
    margin-top: -384px;
    margin-left: auto;
    margin-right: auto;
    width: 658px;
    height: 306px; }
  .questions__text {
    padding-top: 8px; }
  .questions {
    flex-direction: column;
    padding: 0;
    height: 455px; }
  .questions__input {
    width: 269px;
    box-sizing: border-box; }
  .questions__button {
    width: 250px;
    box-sizing: border-box;
    margin-top: 25px; }
  .questions__image {
    width: 100%; } }

@media (max-width: 767px) {
  .questions__image {
    display: none; }
  .questions__form {
    display: flex;
    flex-direction: column;
    width: 100%; }
  .questions__form-wrapper {
    background-color: #fbf8f8;
    position: static;
    width: 100%;
    height: 100%;
    text-align: center;
    padding: 35px 16px;
    margin: 0;
    font-weight: 600;
    font-size: 25px;
    line-height: 32px;
    padding-top: 55px; }
  .questions {
    padding-bottom: 0;
    height: 441px; }
  .questions__title {
    font-weight: 600;
    font-size: 25px;
    line-height: 32px; }
  .questions__text {
    font-size: 16px;
    line-height: 150%;
    padding-top: 22px;
    padding-bottom: 18px; }
  .questions__button,
  .questions__input {
    width: 100%; }
  .questions__input {
    padding: 16px;
    margin-bottom: 18px; }
  .questions__button {
    margin-top: 0;
    padding: 19px;
    margin-bottom: 0; }
  .questions__input--tel {
    padding-left: 30px; }
  .questions__input:placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px; }
  .questions__input-wrapper::after {
    font-size: 14px;
    line-height: 17px;
    font-weight: 400; } }

.success-message {
  position: relative;
  width: 728px;
  height: 444px;
  background-color: #fbf8f8;
  font-weight: 500;
  font-size: 25px;
  line-height: 170%;
  color: #48494d;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center; }

.success-message-wrapper {
  display: none; }

.success-message-wrapper.success-message-wrapper--show {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  z-index: 200; }

.success-message__icon {
  width: 22px;
  height: 22px; }

.success-message__close {
  top: 20px;
  right: 20px;
  position: absolute;
  border: none;
  background-color: transparent;
  cursor: pointer; }
