.next-travel__lists {
  display: grid;
  grid-template-columns: max-content max-content max-content;
  column-gap: 50px;
}

.next-travel {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
}

.next-travel__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.next-travel__item {
  color: rgba($basic-black, 0.5);
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 10px;
}

.next-travel__list:last-child {
  margin-right: 37px;
}

@media (max-width: $tablet-width) {
  .next-travel {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }

  .next-travel__lists {
    column-gap: 48px;
  }

  .next-travel__item {
    padding-bottom: 10px;
  }
}

@media (max-width: $mobile-width) {
  .next-travel__lists {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: 10px;
    padding-bottom: 4px;
    column-gap: 10px;
  }

  .next-travel__list {
    text-align: center;
  }

  .next-travel__list:nth-child(1) {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }

  .next-travel__list:nth-child(2) {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
  }

  .next-travel__list:nth-child(3) {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
    margin: 0 auto;
  }

  .next-travel__item {
    margin: 0;
  }
}
